<template>
	<view class="page">

		<view class="topic">
			<view class="title">已掌握/错题总数：</view>
			<view class="nums">{{ok + '/' + total}}</view>
			<view class="typebutton" @click="selecttype">
				<view class="typestring">{{typearray[selectedindex]}}</view>
				<image class="wrongimage" :src="isselected == true ? unselected:selecetd" mode="aspectFit"></image>
			</view>
		</view>
		<scroll-view class="list" :style="{height : scrollH + 'px'}" scroll-y="true">
			<!-- 0 判断  1 单选  2 定项多选  3 简答   4 填空   5 不定项多选 -->
			<view v-for="(item,index) in wronglist">
				<view class="cell" @click="detailwrong(item)">
					<template v-if="item.type == 0">
						<image :src="item.content" class="content_image" mode="aspectFill"></image>
						<view class="cell_bottom">
							<view class="cell_bottom_time">{{item.addtime}}</view>
							<view class="cell_bottom_title1" v-if="item.status == 0">未掌握</view>
							<view class="cell_bottom_title2" v-if="item.status == 1">已掌握</view>
						</view>
					</template>
					<template v-else>
						<view v-if="item.content.type == 0">
							<view class="list_title">
								<text class="itemtype">（判断题）</text><text>{{item.content.title}}</text>
							</view>
							<template v-if="item.content.t_img">
								<image :src="item.content.t_img" class="t_img"></image>
							</template>
							<view class="cell_bottom">
								<view class="cell_bottom_time">{{item.addtime}}</view>
								<view class="cell_bottom_title1" v-if="item.status == 0">未掌握</view>
								<view class="cell_bottom_title2" v-if="item.status == 1">已掌握</view>
							</view>
						</view>
						<view v-if="item.content.type == 1 || item.content.type == 2 || item.content.type == 5">
							<view class="list_title">
								<text v-show="item.content.type == 1" class="itemtype">（单选）</text><text v-show="item.content.type == 1">{{item.content.title}}</text>
								<text v-show="item.content.type == 2" class="itemtype">（定项多选）</text><text v-show="item.content.type == 2">{{item.content.title}}</text>
								<text v-show="item.content.type == 5" class="itemtype">（不定项多选）</text><text v-show="item.content.type == 5">{{item.content.title}}</text>
								<view v-for="(item2,index2) in item.content.ans" class="ans">
									<view>{{ABCD[index2] + '.'+ item2}}</view>
								</view>
							</view>
							<template v-if="item.content.t_img">
								<image :src="item.content.t_img" class="t_img"></image>
							</template>
							<view class="cell_bottom">
								<view class="cell_bottom_time">{{item.addtime}}</view>
								<view class="cell_bottom_title1" v-if="item.status == 0">未掌握</view>
								<view class="cell_bottom_title2" v-if="item.status == 1">已掌握</view>
							</view>
						</view>
						<view v-if="item.content.type == 3">
							<view class="list_title">
								<text class="itemtype">（简答题）</text><text>{{item.content.title}}</text>
							</view>
							<template v-if="item.content.t_img">
								<image v-if="item.content.t_img.length > 6" :src="item.content.t_img" class="t_img"></image>
							</template>
							<view class="cell_bottom">
								<view class="cell_bottom_time">{{item.addtime}}</view>
								<view class="cell_bottom_title1" v-if="item.status == 0">未掌握</view>
								<view class="cell_bottom_title2" v-if="item.status == 1">已掌握</view>
							</view>
						</view>
						<view v-if="item.content.type == 4">
							<view class="list_title">
								<text class="itemtype">（填空题）</text><text>{{item.content.title}}</text>
							</view>
							<template v-if="item.content.t_img">
								<image v-if="item.content.t_img.length > 6" :src="item.content.t_img" class="t_img"></image>
							</template>
							<view class="cell_bottom">
								<view class="cell_bottom_time">{{item.addtime}}</view>
								<view class="cell_bottom_title1" v-if="item.status == 0">未掌握</view>
								<view class="cell_bottom_title2" v-if="item.status == 1">已掌握</view>
							</view>
						</view>
					</template>

				</view>
			</view>
		</scroll-view>

		<view v-show="showselectview == true" class="showselect">
			<view v-for="(item,index) in typearray" class="menu" @click="menusele(index)">
				<view class="item1" :style="selectedindex == index ? 'color:#64D3AD':'color:#000000'">{{item}}</view>
			</view>
		</view>

		<view class="bottom">
			<image @click="album" src="../../static/wrong_luru.png" class="luru"></image>
		</view>
		<template v-if="kongkong == true">
			<view :class="{xiangziwrap : (kongkong == true)}">
				<image class="xiangzi" src="../../static/images/xiangzi.png" mode="aspectFill"></image>
				<text class="xiangzi_txt">暂无错题</text>
			</view>
		</template>
	</view>
</template>

<script>
	const app = getApp();

	export default {
		data() {
			return {
				imagehhh: '',
				wronglist: [],
				kongkong: false,
				typestring: '全部',
				isselected: false,
				selecetd: '',
				unselected: '',
				showselectview: false,
				typearray: ['全部', '已掌握', '未掌握'],
				selectedindex: 0,
				ok: 0,
				total: 0,
				ABCD: ['A', 'B', 'C', 'D'],
				scrollH: 0
			}
		},
		onShow() {
			this.getlist(this.selectedindex);
		},
		onLoad() {
			this.GetCount();
			this.getlist(this.selectedindex);
			this.selecetd = '../../static/wrong_down.png';
			this.unselected = '../../static/wrong_up.png';
			uni.getSystemInfo({
				success: (res) => {
					this.scrollH = res.windowHeight - 100;
				}
			});
		},
		methods: {
			album() {
				console.log('打开相册');
				var that = this;
				uni.chooseImage({
					count: 1,
					success(res) {
						console.log(res.tempFilePaths[0]);
						that.imagehhh = res.tempFilePaths[0];

						uni.navigateTo({
							url: '../wrong_image/wrong_image?imagepath=' + that.imagehhh,
						});
					}
				});
			},
			detailwrong(itemm) {
				var item = JSON.parse(JSON.stringify(itemm));
				uni.navigateTo({
					url: '../wrongdetail/wrongdetail?type=' + item.type + '&id=' + item.id + '&status=' + item.status,
				});
			},
			menusele(index) {
				this.selectedindex = index;
				var that = this;
				setTimeout(() => {
					this.showselectview = false;
					that.getlist(index);
				}, 300);
			},
			selecttype() {
				this.showselectview = !this.showselectview;
				if (this.showselectview == false) {
					this.isselected = true;
				} else {
					this.isselected = false;
				}
			},
			GetCount() {
				let gData = app.globalData;
				setTimeout(() => {
					uni.request({
						url: gData.site_url + 'Wrongbook.GetCount',
						method: 'GET',
						data: {
							'uid': gData.userinfo.id,
							'token': gData.userinfo.token
						},
						success: res => {
							var info = res.data.data.info[0];
							this.ok = info.ok;
							this.total = info.total;
						}
					});
				}, 0);
			},
			getlist(type) {
				let gData = app.globalData;
				var that = this;
				setTimeout(() => {
					uni.request({
						url: gData.site_url + 'Wrongbook.GetList',
						method: 'GET',
						data: {
							'uid': gData.userinfo.id,
							'token': gData.userinfo.token,
							'status': type
						},
						success: res => {
							// console.log(JSON.parse(JSON.stringify(res)));
							if (res.data.data.code == 0) {
								
								that.wronglist = res.data.data.info;
									
								if (res.data.data.info.legnth == 0){
									that.kongkong = true;
			
								}else{
								    that.kongkong = false;
								}
							} else {
								that.kongkong = true;
							}

						}
					});
				}, 0);
			}
		}
	}
</script>

<style>
	.aaaaa {
		width: 100%;
		height: 50%;
		position: absolute;
		top: 0;
	}

	.luru {
		width: 100rpx;
		height: 100rpx;
		margin-top: -40rpx;
		margin-left: calc(50% - 20px);
	}

	.page {
		position: absolute;
		width: 100%;
		height: 100%;
	}

	.list {
		padding-top: 5rpx;
		background-color: #F5F5F5;
	}

	.bottom {
		width: 100%;
		height: 100rpx;
		bottom: 0rpx;
		position: absolute;
		background-color: #FFFFFF;
	}

	.content_image {
		margin-top: 10rpx;
		width: 100%;
		height: 200rpx;
		border-radius: 8rpx;
	}

	.t_img {
		width: 160rpx;
		height: 160rpx;
		margin-top: 10rpx;
		margin-left: 20rpx;
	}

	.ans {
		margin-left: 20rpx;
		margin-top: 10rpx;
	}

	.itemtype {
		color: #6D6D72;
	}

	.cell_bottom_time {
		color: #CACACA;
		font-size: smaller;
		width: 100%;
	}

	.cell_bottom_title1 {
		color: #6D6D72;
		font-size: 22rpx;
		background-color: #EBEEF5;
		width: 120rpx;
		height: 40rpx;
		text-align: center;
		border-radius: 20rpx;

	}

	.cell_bottom_title2 {
		font-size: 22rpx;
		width: 120rpx;
		height: 40rpx;
		text-align: center;
		border-radius: 20rpx;
		color: #FFFFFF;
		background-color: #64D3AD;
	}

	.cell_bottom {
		margin-top: 10rpx;
		right: 20rpx;
		display: flex;
		flex-direction: row;
		margin-left: 20rpx;
		margin-right: 20rpx;
		margin-bottom: 10rpx;

	}

	.cell {

		margin-left: 20rpx;
		margin-right: 20rpx;
		height: auto;
		margin-top: 20rpx;
		border: 2rpx solid #CACACA;
		background-color: #FFFFFF;
		border-radius: 8rpx;
	}

	.list_title {
		margin-top: 20rpx;
		margin-left: 20rpx;
		margin-right: 20rpx;
		border: 2rpx solid #CACACA;
		border-radius: 8rpx;

	}



	.menu {
		width: 100%;
		height: 70rpx;
	}

	.item1 {
		padding-right: 20rpx;
		color: #FFFFFF;
		text-align: right;

	}

	.showselect {
		width: 100%;
		position: absolute;
		top: 70rpx;
		z-index: 1;
		background-color: #FFFFFF;
		height: 200rpx;
		padding-top: 20rpx;
	}

	.title {
		margin-left: 20rpx;
		margin-top: 20rpx;
		color: #6D6D72;
	}

	.nums {
		margin-top: 20rpx;
	}

	.topic {
		position: relative;
		top: 0rpx;
		height: 90rpx;
		background-color: #FFFFFF;
		display: flex;
		flex-direction: row;
	}

	.typebutton {
		width: 140rpx;
		height: 35rpx;
		border-radius: 20rpx;
		border: 2rpx solid #64D3AD;
		color: #64D3AD;
		position: absolute;
		right: 20rpx;
		margin-top: 30rpx;
	}

	.typestring {
		margin-top: 20rpx;
		text-align: center;
		color: #64D3AD;
		height: 30rpx;
		padding-right: 30rpx;
		margin-top: -5rpx;
		font-size: smaller;
	}

	.wrongimage {
		position: absolute;
		width: 20rpx;
		height: 20rpx;
		right: 20rpx;
		top: 7rpx;
	}

	.xiangziwrap {
		position: absolute;
		left: calc(50% - 80px);
		top: calc(50% - 100px);
		width: 300rpx;
		height: 100rpx;
	}
	.xiangzi {
		margin-left: 100rpx;
		width: 100rpx;
		height: 100rpx;
	}

	.xiangzi_txt {
		width: 100%;
		display: block;
		text-align: center;
		font-size: 18rpx;
		color: #C7C7C7;
	}
</style>
